//        - main-content,  ,
// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.main-content {
  $block-name: &; // #{$block-name}__element
  padding-top: 30px;
  margin-bottom: 40px;
  background: #fff;

  @media (min-width: $screen-md) {
    padding-top: 80px;
    margin-bottom: 62px;
  }

  &__inner {
    @include container();
  }

  &__title {
    font-weight: 300;
    margin-top: 0;
    margin-bottom: 15px;
    font-size: 28px;
    line-height: 1.2;
    letter-spacing: 0.4em;
    text-transform: uppercase;
    text-align: center;

    @media (min-width: $screen-sm) {
      font-size: 32px;
    }

    @media (min-width: $screen-md) {
      font-size: 48px;
      margin-bottom: 23px;
    }

    @media (min-width: $screen-lg) {
      font-size: 60px;
    }
  }

  &__subtitle {
    font-weight: 300;
    color: $gray--lightest;
    font-style: italic;
    font-size: 16px;
    text-align: center;

    @media (max-width: $screen-sm-max) {

      > span {
        display: block;
      }
    }

    @media (min-width: $screen-md) {
      font-size: 20px;
    }

    @media (min-width: $screen-lg) {
      font-size: 22px;
    }
  }

  &__heart {
    color: #ff0000;
    font-weight: 300;
  }
}
