//        - top-nav-toggler,  ,
// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.top-nav-toggler {
  $block-name:                &; // #{$block-name}__element
  position: relative;
  width: 20px;
  height: 20px;
  background: url('../img/icon-info.svg') center center no-repeat;
  background-size: contain;
  font-size: 0;
  opacity: 0.5;
  z-index: 1500;

  &--close {
    opacity: 1;
  }

  &--disabled {
    pointer-events: none;
  }

  @media (min-width: $screen-md) {
    display: none;
  }
}
