//        - default-page,  ,
// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.default-page {
  $block-name:                &; // #{$block-name}__element
  padding-top: 18px;

  @media (min-width: $screen-lg) {
    padding-top: 32px;
  }

  &__inner {
    @include container();
  }

  .breadcrumbs {
    margin-bottom: 6px;

    @media (min-width: $screen-lg) {
      margin-bottom: 20px;
    }
  }
}
