//        - logo,  ,
// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.logo {

  $block-name: &; // #{$block-name}__element
  display: inline-block;
  white-space: nowrap;
  padding-top: 3px;

  @media (min-width: $screen-md) {
    padding-top: 0;
  }

  img {
    display: block;
    width: 110px;
    height: 21px;

    @media (min-width: $screen-lg) {
      width: 166px;
      height: 30px;
    }
  }
}
